@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Graduate&display=swap);
input[type=text], select {
    width: 19rem;
    max-width: 20rem;
    padding: 12px 0.4rem;
    margin: 7px 0;
    display: flex;
    text-align: center;
    border: 2px solid rgb(8, 159, 230);
    border-radius: 4px;
    box-sizing: border-box;
    background-color: #F8F8F8;
    font-size: 1.2rem;
    
  }
  .input-block{
      
      display: flex;
      align-items: center;
    flex-direction: column;

  }
  .input-block label{
      font-size: 14px;
      font-family: 'IBM Plex Mono', monospace;
  }
  .input-block input{
    
    border: 2px solid #ccc;
    width: 18rem;
    background-color: #E9EBF2;
    height: 5rem;
    max-width: 20rem;
    padding: 12px 12px;
    font-size:3rem;
    text-align: center;
  }
  
.page{
    background: #E9EBF2;
    flex: 1 1;
}
.header{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 2rem;
}
.titulo{
    max-width: 200px;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    font-family: 'Graduate';
  
    
}
.imagem{
    
    width: 90px;
    height: 90px;
}
.button{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20rem;
    font-size: 2rem;
    color: #ffffff;
    margin: 3rem 0rem;
    height: 60px;
    border-radius: 1rem;
    border: #017ee4;
    background-color: #017ee4;
    font-family: 'Graduate';
    transition: 0.2s;
  
}
.body button:hover{
    width: 21rem;
    height: 65px;
}
.body{
    margin-top: 1rem;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.body Input{
    margin-bottom: 4rem;
}
.btn{
    display: flex;
    align-items: center;
    flex-direction: column;
}
@media(min-width:700px){
    .body{
        margin-top: 5rem;
        display: flex;
       padding-right: 2rem;
       padding-left: 2rem;
        align-items: center;
        justify-content: space-around;
        flex-direction: row;
    }
    .btn{
        display: flex;
        flex-direction: column;
    }
}
